.nav{
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 20px;
    height: 30px;
    z-index: 1;

    transition-timing-function: ease-in;
    transition: all 0.5s;

}

.nav_black{
    background-color: #111;
}

.netflix-logo{
    width: 80px;
    object-fit: contain;
    position: fixed;
    left: 20px;

}

.avatar{
    width:30px;
    object-fit: contain;
    position: fixed;
    right: 20px;
}